.Button {
    background-color: transparent;
    border: none;
    color: white;
    outline: none;
    cursor: pointer;
    font: inherit;
    padding: 10px;
    margin: 10px;
    font-weight: bold;
}
.Button:first-of-type {
    margin-left: 0;
    padding-left: 0;
}
.Button:disabled{
  color: #CFD8DC;
  cursor: not-allowed;
}
.Success {
    color: #5C9210;
}
.Danger {
    color: #944317;
}



